@import "../../App.scss";

.comments {
  @include themify($themes) {
    .comment {
        margin: 30px 0px;
        display: flex;
        justify-content: space-between;
        gap: 20px;

        .info {
            flex:5;
            display: flex;
            flex-direction: column;
            gap: 3px;
            align-items: flex-start;
    
            span {
              font-weight: 500;
            }
    
            p{
              color:themed("textColorSoft")
            }
          }
    
          .date{
            flex:1;
            align-self: center;
            color: gray;
            font-size: 12px;
          }
      }
  }
}